import React from 'react'

import defaults from '../_defaults'


/*
 * COMPONENTS
 *
 */
function HeatingSetTemperatureBody(props) {
	const Level = defaults.components.LevelComponent;
	return (
		<Level min={0} max={35} {...props} />
	);
}


export default {
	body: {
		HeatingSetTemperatureBody
	},
	action: {
	}
}