import Log from './Log'
import i18n from './i18n'

const _copy = require('rfdc')();


/**
 * Settings
 *
 *
 */
export default class Settings {

	/*
	 * holds the layout
	 */
	static layout = [];
	
	/*
	 * holds the settings
	 */
	static settings = {};
	
	/**
	 * Apply settings.
	 *
	 * @param	{Function}	reRender
	 * @return	void
	 */
	static applySettings() {
		
		// language
		i18n.setTranslations(language => require('../i18n/' + language));
		if (Settings.settings.language) {
			i18n.setLanguage(Settings.settings.language);
		}
		
		// page title
		if (Settings.settings.pageTitle) {
			document.title = Settings.settings.pageTitle;
		}
		
		// favicon
		if (Settings.settings.pageFavicon && Settings.settings.pageFavicon.substr(0, 5) === 'data:') {
			document.getElementById('favicon').href = Settings.settings.pageFavicon;
		}
		
		// set user specific translations
		if (Settings.settings.translations) {
			
			let translations = Settings.settings.translations;
			for (let language in translations) {
				i18n.setTranslation(language, translations[language]);
			}
		}
	}
	
	/**
	 * Gets a setting.
	 *
	 * @param	{String}	settings
	 * @return	{String}
	 */
	static getSetting(setting) {
		return Settings.settings[setting] || '';
	}
	
	/**
	 * Gets all settings.
	 *
	 * @param	void
	 * @return	{Object}
	 */
	static getSettings(copy = true) {
		return copy ? _copy(Settings.settings || {}) : (Settings.settings || {});
	}
	
	/**
	 * Gets the layout.
	 *
	 * @param	void
	 * @return	{Object}
	 */
	static getLayout(copy = true) {
		return copy ? _copy(Settings.layout || {}) : (Settings.layout || {});
	}
	
	/**
	 * Sets a setting.
	 *
	 * @param	{String}	setting
	 * @param	{String}	value
	 * @return	{Boolean}
	 */
	static setSetting(setting, val) {
		
		if (setting && val !== undefined) {
			Settings.settings[setting] = val;
			return true;
		}
		
		return false;
	}

	/**
	 * Set layout.
	 *
	 * @param	{Object}	layout
	 * @return	{Boolean}
	 */
	static setLayout(layout) {
		if (layout) {
			Settings.layout = [ ...Settings.layout, ...layout || [] ];
			Log._('Settings', [ 'Set layout', Settings.layout ]);
			return true;
		}
		
		return false;
	}

	/**
	 * Set settings.
	 *
	 * @param	{Object}	settings
	 * @return	{Boolean}
	 */
	static setSettings(settings) {
		if (settings) {
			Settings.settings = { ...Settings.settings, ...settings || {} };
			Log._('Settings', [ 'Set settings', Settings.settings ]);
			return true;
		}
		
		return false;
	}
}
