
const color = {
	icon: {
		'default': 'palette',
	},
	unit: ' '
}

export default {
	power: {
		body: null,
		action: 'SwitchAction',
		
		icon: {
			'true': 'lightbulb-on',
			'false': 'lightbulb-off-outline'
		},
	},
	level: {
		body: 'LevelBody',
		action: null,
		
		value: val => val > 0 && val <= 1 ? val * 100 : val,
		unit: val => val > 0 ? ' %' : null,
		stateStyle: {
			'0': {
				'color': '#999'
			}
		},
		icon: {
			'default': 'lightbulb-on',
			'0': 'lightbulb-off-outline'
		},
		display: {
			'0': 'light#level#off'
		},
	},
	ct: {
		icon: {
			'default': 'thermometer',
		},
		unit: ' K'
	},
	colorTemperature: {
		body: 'LightColorTemperatureBody',
		action: null,
		
		icon: {
			'default': 'thermometer',
		},
		unit: ' K'
	},
	hue: {
		body: 'LightHueBody',
		action: 'LightColorAction',
		...color
	},
	rgb: {
		body: 'LightColorBody',
		action: 'LightColorAction',
		...color
	},
	hsv: {
		body: 'LightColorBody',
		action: 'LightColorAction',
		...color
	},
	hex: {
		body: 'LightColorBody',
		action: 'LightColorAction',
		...color
	}
}