import getActivityType from './blind-manufacturers'

export default {
	level: {
		value: val => Math.round(val),
		icon: {
			'default': 'window-shutter-open',
			'>90': 'window-shutter-open',
			'<=90': 'window-shutter'
		}
	},
	level2: {
		value: val => Math.round(val),
		unit: ' %',
		icon: {
			'default': 'window-shutter-open',
			'>90': 'window-shutter-open',
			'<=90': 'window-shutter'
		}
	},
	activity: {
		val: (val, stateKey, device) => getActivityType(device.attributes.manufacturer, val),
		icon: {
			true: 'pan-vertical',
			false: 'dots-vertical',
		},
		display: {
			true: 'blind#activity#true',
			false: 'blind#activity#false',
		}
	}
}