import i18n from '../controller/i18n'


export const FunctionList = [
	'defaults',
	'blind',
	'door',
	'heating',
	'light',
	'location',
	'user',
	'window'
];


let funConfigurations = {}
let funcComponents = {
	body: {},
	action: {}
}

FunctionList.forEach(func => {
	func = func === 'defaults' ? '_' + func : func;
	
	// load function language file
	try {
		i18n.setTranslations(language => require('./' + func + '/i18n/' + language));
	}
	catch(err) {
		//console.error(err);
	}
	
	// load function itself
	try {
		funConfigurations[func] = require('./' + func).default;
		
		let elements = require('./' + func + '/components').default
		funcComponents.body = { ...funcComponents.body, ...elements.body }
		funcComponents.action = { ...funcComponents.action, ...elements.action }
	}
	catch(err) {
		//console.error(err);
	}
});

export default funConfigurations;
export const components = funcComponents;
