import React, { useState, useEffect } from 'react'
import clsx from 'clsx'
import color from 'color'

import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'
import ExpandLessIcon from '@material-ui/icons/ExpandLess'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import StopIcon from '@material-ui/icons/Stop'

import Log from '../../controller/Log'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	value: props => ({
		float: 'left',
		lineHeight: '32px',
		marginRight: '10px'
	}),
	borderLeftTransparent: {
		borderLeftColor: 'transparent !important'
	},
	borderLeftColor: {
		borderLeftColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightTransparent: {
		borderRightColor: 'transparent !important'
	},
	borderRightColor: {
		borderRightColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightColorSecondary: {
		borderRightColor: color(theme.palette.secondary.main).alpha(0.5).rgb().toString() +  '!important'
	}
}));


/*
 * ACTIONS
 *
 */
function BlindLevelAction(props) {
	const { device, state, stateKey } = props;
	const classes = useStyles();
	
	const onGoUp = () => {
		setLevel(100);
		setActivity(true);
		device.setDeviceState(stateKey, 100).catch(err => Log._('Function (blind)', err.message, 'error', true, err.stack));
	}
	
	const onGoDown = () => {
		setLevel(0);
		setActivity(true);
		device.setDeviceState(stateKey, 0).catch(err => Log._('Function (blind)', err.message, 'error', true, err.stack));
	}
	
	const onStop = () => {
		setActivity(false);
		device.setDeviceState('activity', true).catch(err => Log._('Function (blind)', err.message, 'error', true, err.stack));
	}
	
	// level
	const [level, setLevel] = useState(state.value || null);
	useEffect(() => setLevel(state.value), [state]);
	
	// activity
	const [activity, setActivity] = useState(false);
	const stateChangeActivity = (sKey, s, d) => { sKey === 'activity' && setActivity(s.val); }
	useEffect(() => {
		
		if (device.states.activity && device.states.activity.state) {
			device.states.activity.state = typeof device.states.activity.state === 'string' ? { 'node': device.states.activity.state } : device.states.activity.state;
			
			// request device state
			device.requestDeviceState('activity', true).catch(err => Log._('Function (blind)', err.message, 'error', true, err.stack));
			
			// listen for updates
			device.listen(stateChangeActivity)
		}
		
		// componentWillUnmount
		return () => device.unlisten(stateChangeActivity);
		
	}, [device, state]);
	
	return (

<React.Fragment>
	
	<div className={classes.value}>
		{level + ' ' + state.unit}
	</div>
	
	<ButtonGroup size="small">
		<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderRightColor : classes.borderRightTransparent))} onClick={onGoUp}><ExpandLessIcon /></Button>
		{activity !== null && <Button color="secondary" className={clsx(!activity ? [ classes.borderLeftTransparent, classes.borderRightTransparent ] : classes.borderRightColorSecondary)} onClick={onStop} disabled={!activity}><StopIcon /></Button>}
		<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderLeftColor : classes.borderLeftTransparent))} onClick={onGoDown}><ExpandMoreIcon /></Button>
	</ButtonGroup>
</React.Fragment>

	);
}


export default {
	body: {
	},
	action: {
		BlindLevelAction
	}
}
