import React, { useState, useEffect } from 'react'

import i18n from '../../controller/i18n'
import Inputs from '../../components/Inputs'
import { components } from '../../functions'

import Checkbox from '@material-ui/core/Checkbox'
import Grid from '@material-ui/core/Grid'
import Switch from '@material-ui/core/Switch'

import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank'
import CheckBoxIcon from '@material-ui/icons/CheckBox'


export default {
	devices: ['device', 'divider', 'group'],
	DeviceBodyConfiguration,
	DeviceActionsConfiguration
}

/*
 *
 */
function DeviceBodyConfiguration({ device, options, onChange, elements = {} }) {
	const [bodyStateKey,setBodyStateKey] = useState(options.bodyStateKey || null);
	useEffect(() => {
		setBodyStateKey(options.bodyStateKey);
	}, [options.bodyStateKey]);
	
	if (options.type !== 'device') {
		return <Inputs.Fieldset>
			<Inputs.FieldsetItem>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	}
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Body'}>
			<Inputs.FieldsetItem container>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
			
			<Inputs.FieldsetItem columns={12} noPadding>
				<Inputs.Text
					key='label'
					id='label'
					width={150}
					
					placeholder={device.name}
					val={options.label || ''}
					label='Device Label'
					onChange={onChange}
					/>
				
				<Inputs.Text
					key='bodyStateKey'
					id='bodyStateKey'
					width={140}
					
					autoComplete
					autoHighlight
					
					val={bodyStateKey}
					label='Body State Key'
					options={Object.keys(device.states || {})}
					onChange={(props, val) => {
						setBodyStateKey(val);
						onChange(props, val);
					}}
					
					renderOption={(option, state) => {
						option = !option ? <em>{i18n.t('no body element')}</em> : option;
						return state.selected ? <strong>{option}</strong> : option;
					}}
					/>
				
				{bodyStateKey !== null && <Inputs.Text
					key='bodyElement'
					id='bodyElement'
					width={200}
					
					autoComplete
					autoHighlight
					
					val={options.bodyElement || null}
					label='Body Element'
					onChange={onChange}
					options={Object.keys(components.body || {})}
					/>}
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}

/*
 *
 */
export function DeviceActionsConfiguration({ device, options, onChange, elements = {} }) {
	let deviceStates = Object.keys(device.states || {});
	
	const [checked,setChecked] = useState(options.actionType === 'action');
	const [actionStateKey,setActionStateKey] = useState(options.primaryStateKey || (deviceStates && deviceStates[0]) || null);
	
	const icon = <CheckBoxOutlineBlankIcon fontSize="small" />
	const checkedIcon = <CheckBoxIcon fontSize="small" />
	
	if (options.type !== 'device') {
		return null;
	}
	
	let actionElementOptions = Object.keys(components.action || {});
	actionElementOptions.splice(0, 1); // components.action.State;
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Action'}>
			<Inputs.FieldsetItem>
				<Grid component="label" container alignItems="center">
					<Grid item>{i18n.t('Show states')}</Grid>
					<Grid item>
						<Switch
							color="default"
							size="small"
							checked={checked}
							onChange={(e, val) => {
								onChange({ id: 'actionType' }, val === true ? 'action' : 'state');
								
								// delete error of Action Element when switching to State
								if (val !== true) {
									onChange({ id: 'actionElement', error: null }, '');
								}
								
								// initial error of Action Element
								else {
									onChange({ id: 'actionElement', error: !options.actionElement }, '');
								}
								
								setChecked(prevChecked => prevChecked !== true);
							}}
							/>
							
					</Grid>
					<Grid item>{i18n.t('Show an action element')}</Grid>
				</Grid>
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem noPadding>
				
				{!checked && <>
				<Inputs.Text
					key='primaryStateKey'
					id='primaryStateKey'
					width={140}
					required
					
					autoComplete
					autoHighlight
					
					val={actionStateKey}
					label='Primary State Key'
					onChange={(props, val) => {
						setActionStateKey(val);
						onChange(props, val);
					}}
					options={Object.keys(device.states || {})}
					/>
					
				<Inputs.Text
					key='secondaryStatesKeys'
					id='secondaryStatesKeys'
					width={270}
					
					autoComplete
					autoHighlight
					multiple
					limitTags={2}
					disableCloseOnSelect
					size="small"
					
					val={options.secondaryStatesKeys || []}
					label='Secondary States Keys'
					onChange={onChange}
					options={Object.keys(device.states || {})}
					
					renderOption={(option, { selected }) => option && (
						<React.Fragment>
							<Checkbox
								icon={icon}
								checkedIcon={checkedIcon}
								style={{ marginRight: 8 }}
								checked={selected}
								/>
							
							{option}
						</React.Fragment>
					)}
					/>
				</>}
				
				{checked && <>
				<Inputs.Text
					key='actionStateKey'
					id='primaryStateKey'
					width={140}
					required
					
					autoComplete
					autoHighlight
					
					val={actionStateKey}
					label='Action State Key'
					options={Object.keys(device.states || {})}
					onChange={(props, val) => {
						setActionStateKey(val);
						onChange(props, val);
					}}
					
					renderOption={(option, state) => {
						option = !option ? <em>{i18n.t('no action element')}</em> : option;
						return state.selected ? <strong>{option}</strong> : option;
					}}
					/>
				
				{actionStateKey !== null && <Inputs.Text
					key='actionElement'
					id='actionElement'
					width={200}
					required
					
					autoComplete
					autoHighlight
					
					val={options.actionElement || null}
					label='Action Element'
					onChange={onChange}
					options={actionElementOptions}
					/>}
				</>}
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}
